.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba($color: #000000, $alpha: 0.4);
  display: flex;
  z-index: 1001;
  padding: 0px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s ease;
  overflow-y: auto;
  overscroll-behavior-y: contain;

  @media screen and (min-width: 481px) {
    padding: 20px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__wrapper {
    max-width: 480px;
    position: relative;
    width: 100%;
    background: #fff;
    padding: 20px;
    align-items: flex-start;
    margin: auto 0 0 0;

    @media screen and (min-width: 481px) {
      margin: auto;
      padding: 40px;
    }
  }

  &__content {
    .section-h2 {
      text-align: center;
      margin-bottom: 35px;
      font-size: 36px;
      line-height: 110%;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 24px;
    height: 24px;

    a {
      display: block;
    }
  }
}
